import { Navigate } from 'react-router-dom';

import '../App.css';

function Cabinet({base_url, isLogin}) { 
  return (
    <>
    {!isLogin && <Navigate replace to={'/'} />}
    <div className='cabinet-header'>
      <h1>ЛИЧНЫЙ КАБИНЕТ</h1>
    </div>
      <div className='cabinet-block'>
        <div className='cabinet-content-block'>
          <div className='inner'>
            <table width="100%" className='order-items'>
              <tbody>
                <tr>
                  <td className='cell'>ТИП СДЕЛКИ</td>
                  <td className='cell'>ID</td>
                  <td className='cell'>ДАТА</td>
                  <td className='cell'>СУММА</td>
                  <td className='cell'>РЕКВИЗИТЫ</td>
                  <td className='cell'>СТАТУС</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}

export default Cabinet;