import { Routes as Reactroutes, Route } from "react-router-dom";
import Home from "./pages/home";
import SellPage from "./pages/SellPage";
import Reviews from "./pages/Reviews";
import Cabinet from "./pages/Cabinet";

function Routes({base_url, response, isLogin} ) { 
 
  return (
    <Reactroutes>
        <Route path="/" element={ <Home/> }/> 
        <Route path="/sell" element={<SellPage isLogin={isLogin} base_url={base_url}/>}/>
        <Route path="/reviews" element={<Reviews isLogin={isLogin} base_url={base_url} response={response}/>}/>
        <Route path="/cabinet" element={<Cabinet isLogin={isLogin} base_url={base_url}/>}/>
    </Reactroutes>
  );
}

export default Routes;